import React from 'react'
import { Slider, Rail, Handles, Tracks } from 'react-compound-slider'
import { KeyboardHandle, SliderRail, Track } from './SliderComponents.js'
import { Row, Col } from "react-bootstrap"

function preventDefault(e){
    e.preventDefault();
}

function disableScroll(){
    document.body.addEventListener('touchmove', preventDefault, { passive: false });
}
function enableScroll(){
    document.body.removeEventListener('touchmove', preventDefault);
}

const sliderStyle = {
  position: 'relative',
  width: 25,
  height: 256,
  left: 0,
  border: 0,
  touchAction: 'none'
}
class Fader extends React.Component {
  constructor(props) {
    super(props);
    if(this.props.def.log === true) {
      this.domain = [0, 1];
      this.factor = Math.log(this.props.def.max/this.props.def.min);
      console.log(this.factor);
      this.props.def.step=0.01;
    }
    else {
       this.domain = [this.props.def.min, this.props.def.max];
    }
    this.state = {
      values: [this.fromVal(this.props.def.defval)],
      updates: [this.fromVal(this.props.def.defval)]
    }
  }

  fromVal(value) {
    if(this.props.def.log === true) {
      let ret = Math.log(value/this.props.def.min)/this.factor;
      return ret;
    }
    return value;
  }


  toVal(value) {
    if(this.props.def.log === true) {
      let ret = this.props.def.min * Math.exp(this.factor*value);
      return ret;
    }
    return value;
  }

  componentDidMount() {
    //this.sender.register(this.props.def.osc, this);
    //this.sender.send(this.props.def.osc, this.toVal(this.state.values[0]));
  }

  onUpdate = updates => {
    //updates[0] = this.formatVal(updates[0]);
    this.setState({ updates })
    this.props.onChange(this.props.def.id, this.toVal(updates[0]));
  }

  onChange = values => {
    //values[0] = this.formatVal(values[0]);
    this.setState({ values })
  }

  setValue = (value) => {
    let values = this.state.values.slice(0);
    values[0] = this.fromVal(value);
    this.setState({ values })
    let updates = this.state.updates.slice(0);
    updates[0] = this.fromVal(value);
    this.setState({ updates })
    this.sender.send(this.props.def.osc, value);
  }
  formatVal(val) {
    if(this.props.def.log === true) {
      if(val>6)
      return Math.floor(val);
      return Math.floor(val*1000)/1000;
    }
    return Math.floor(val*1000)/1000;
  }
  render() {
    const {
      state: { values, updates },
    } = this
    return (
      <Col className='colfader'>
      <Row noGutters='true' ><Col>{this.props.def.label}</Col></Row>
      <Row noGutters='true'><Col>
      <Slider
          rootStyle={sliderStyle}
          domain={this.domain} // [min, max]
          step={this.props.def.step}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          onSlideStart={disableScroll}
          onSlideEnd={enableScroll}
          values={values}
          vertical
          reversed
      >
        <Rail>
            {({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}
        </Rail>
        <Handles>
          {({ handles, getHandleProps }) => (
            <div className="slider-handles">
                {handles.map(handle => (
                  <KeyboardHandle
                    domain={this.domain}
                    key={handle.id}
                    handle={handle}
                    getHandleProps={getHandleProps}
                  />
                ))}
            </div>
          )}
        </Handles>
        <Tracks left={false} right={false}>
          {({ tracks, getTrackProps }) => (
            <div className="slider-tracks">
            {tracks.map(({ id, source, target }) => (
              <Track
                key={id}
                source={source}
                target={target}
                getTrackProps={getTrackProps}
              />
            ))}
            </div>
          )}
        </Tracks>
      </Slider>
      </Col>
      </Row>
      <Row noGutters='true'><Col>{this.formatVal(this.toVal(this.state.updates[0]))}</Col></Row>
      </Col>
    )
  }
}

export default Fader;
