import React from 'react'
import { Button, Row, Col } from 'react-bootstrap'
import Fader from './Fader.js'
class DCSound extends React.Component {
  constructor(props) {
      super(props);
      this.state={csoundLoaded:"No",seed:0,pathseed:0,wave:0,scale:0};
  }
  componentDidMount() {
     //global.CsoundObj &&
      global.CsoundObj.initialize().then(() => {
      this.csound = new global.CsoundObj();
  //use this code to load a .csd file and have Csound compile and start it
      fetch('/vcohousegen_all.csd').then((response) => {
        response.arrayBuffer().then(async (buf) => {
        this.csound.writeToFS('vcohousegen_all.csd', buf);
        this.csound.compileCSD('vcohousegen_all.csd');
        this.csound.start();
        this.setState({csoundLoaded:"Yes"});
        this.started = true;
	      this.isPlaying = false;
        })
      })
      });
   }
  handleStart() {
     if(!this.isPlaying) {
       this.csound.readScore('i "trigDrums" 0 3000');
       this.csound.readScore('i "hvs" 0.00067 3000 110 4 0 0 -1 -1');
       this.isPlaying = true;
       let self = this;
       setTimeout(()=> {
          self.csound.requestControlChannel("seed", () => self.setState({seed : self.csound.getControlChannel("seed")}))
          self.csound.requestControlChannel("pathseed", () => self.setState({pathseed : self.csound.getControlChannel("pathseed")}))
          self.csound.requestControlChannel("wave", () => self.setState({wave : Math.trunc(self.csound.getControlChannel("wave"))}))
          self.csound.requestControlChannel("scale", () => self.setState({scale : Math.trunc(self.csound.getControlChannel("scale"))}))
       },500);
     }
  }

  handlePause() {
     if(this.started) {
       this.started = false;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.suspend();
     }
  }

  handleContinue() {
     if(!this.started) {
       this.started = true;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.resume();
     }
  }

  handleStop() {
     if(this.isPlaying) {
       this.csound.readScore('i "trigOff" 0 0.1');
       this.isPlaying = false;
     }
  }

  handleUpdate(id,val) {
    this.csound.setControlChannel(id,val);
  }

  render() {
     var itm = { id : 'hvsamp', type : 'fader', label : 'amp', min: 0, max: 1, step: 0.01, defval: 1 }
     var itmrev = { id : 'hvsrev', type : 'fader', label : 'reverb', min: 0, max: 1, step: 0.01, defval: 0.6 }
     var itmdel = { id : 'hvsdel', type : 'fader', label : 'delay', min: 0, max: 1, step: 0.01, defval: 0.1 }
     var itmdrums = { id : 'drums', type : 'fader', label : 'drums', min: 0, max: 1, step: 0.01, defval: 0.5 }
     const {
       state: { seed,pathseed,wave,scale,csoundLoaded },
     } = this
 
    return (
      <>
      <Row>
	      <Col><Button onClick={this.handleStart.bind(this)}>Start</Button></Col>
	      <Col><Button onClick={this.handlePause.bind(this)}>Pause</Button></Col>
	      <Col><Button onClick={this.handleContinue.bind(this)}>Continue</Button></Col>
	      <Col><Button onClick={this.handleStop.bind(this)}>Stop</Button></Col>
      </Row>
      <Row><Col>seed:{seed}</Col><Col>pseed:{pathseed}</Col><Col>wave:{wave}</Col><Col>scale:{scale}</Col></Row>
      <Row>
        <Col><Fader def={itm} key={itm.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={itmrev} key={itmrev.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={itmdel} key={itmdel.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={itmdrums} key={itmdrums.id} onChange={this.handleUpdate.bind(this)}/></Col>
      </Row>
      <Row><Col>Csound loaded: {csoundLoaded}</Col></Row>
      </>
    );

  }
}

export default DCSound
